<template>
  <div class="modifyPwd">
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="400px"
      :close-on-click-modal="false"
      @close="close"
    >
      <el-form
        :model="userForm"
        ref="userForm"
        :rules="rules"
        label-width="auto"
      >
        <el-row :gutter="20">
          <el-col :span="24">
            <el-form-item label="" prop="oldPwd">
              <el-input
                v-model="userForm.oldPwd"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="" prop="newPwd">
              <el-input
                v-model="userForm.newPwd"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="" prop="newAgainPwd">
              <el-input
                v-model="userForm.newAgainPwd"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { modifyPwd } from "@/api/user";
export default {
  name: "modifyPwd",
  components: {},
  data() {
    let validateAgainPwd = (rule, value, callback) => {
      let newPwd = this.userForm.newPwd;
      if (value == "") callback(new Error(""));
      else if (value != newPwd) callback(new Error(""));
      else callback();
    };
    return {
      dialogVisible: false,
      userForm: {
        userId: "",
        oldPwd: "",
        newPwd: "",
        newAgainPwd: "",
      },
      rules: {
        oldPwd: [
          { required: true, message: "", trigger: "blur" },
        ],
        newPwd: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^[a-zA-Z0-9]{6,18}$/,
            message: " 6-18 ",
            trigger: "blur",
          },
        ],
        newAgainPwd: [{ validator: validateAgainPwd, trigger: "blur" }],
      },
    };
  },
  created: function () {},
  mounted: function () {},
  methods: {
    show(row) {
      this.dialogVisible = true;
      this.userForm.userId = row.userId;
    },
    close() {
      this.dialogVisible = false;
      (this.userForm = {
        userId: "",
        oldPwd: "",
        newPwd: "",
        newAgainPwd: "",
      }),
        this.$nextTick(() => {
          this.$refs.userForm.clearValidate(); //
        });
    },
    submitForm() {
      this.$refs.userForm.validate(async (valid) => {
        if (valid) {
          let res = await modifyPwd(this.userForm);
          if (res.status == 200) {
            this.$message.success(res.message);
            this.close();
          } else {
            this.$message.warning(res.message);
          }
        } else {
          return false;
        }
      });
    },
  },
};
</script>

