/**
 * token
 */

import {
	login,
	logout
} from '@/api/personal'
import {
	getUserInfo
} from '@/api/user'
import {
	getToken,
	setToken,
	removeToken
} from '@/utils/auth'

// 
const state = {
	token: getToken(),
	userInfo: {},
	permissions: []
}
// 
const mutations = {
	SET_TOKEN: (state, token) => {
		state.token = token;
	},
	SET_USER_INFO: (state, userinfo) => {
		state.userInfo = userinfo;
	},
	SET_PERMISSIONS: (state, permissions) => {
		state.permissions = permissions;
	}
}
// mutations
const actions = {
	login({
		commit
	}, userInfo) {
		return new Promise((resolve, reject) => {
			login(userInfo).then(res => {
				const {
					data
				} = res;
				commit('SET_TOKEN', data.token);
				setToken(data.token);
				resolve(res)
			}).catch(error => {
				reject(error);
			})
		})
	},
	logout({
		dispatch
	}) {
		return new Promise((resolve) => {
			logout().then(res => {
				dispatch('resetToken')
				resolve(res)
			}).catch(error => {
				reject(error);
			})
		})
	},
	resetToken({
		commit
	}) {
		return new Promise(resolve => {
			commit('SET_TOKEN', '')
			removeToken();
			resolve()
		})
	},
	getUserInfo({
		commit,
		state
	}) {
		return new Promise((resolve, reject) => {
			getUserInfo({
				token: state.token
			}).then(res => {
				const {
					data
				} = res;
				commit('SET_USER_INFO', data);
				commit('SET_PERMISSIONS', data.operateList);
				resolve(res)
			}).catch(error => {
				reject(error);
			})
		})
	}
}
// store
export default {
	state,
	mutations,
	actions
};