/**
 * 
 * @param {Object} format 
 * yyyy=M,MM=d,dd=dddd=
 * H,HH=m,mm=s,ss=
 * S=Q=
 * yyyyMMdd dddd HH:mm:ss S
 * 
 *console.log(new Date().format("yyyyMMdd dddd HH:mm:ss"));
 */
Date.prototype.format = function (format) {
	var weekArr = ["", "", "", "", "", "", ""];
	var o = {
		"y+": this.getFullYear(), //
		"M+": this.getMonth() + 1, //
		"d+": this.getDate(), //
		"h+": this.getHours(), //
		"m+": this.getMinutes(), //
		"s+": this.getSeconds(), //
		"S": this.getMilliseconds(), //
		"ddd+": this.getDay(), //
		"Q": Math.floor((this.getMonth() + 3) / 3), //
	};
	for (var k in o) {
		if (k == "y+" && new RegExp("(" + k + ")", "i").test(format)) { //
			format = format.replace(RegExp.$1, (o[k] + "").substr(4 - RegExp.$1.length));
		} else if (k == "ddd+" && new RegExp("(" + k + ")", "i").test(format)) { //
			format = format.replace(RegExp.$1, (RegExp.$1.length == 3) ? (o[k]) : (weekArr[o[k]]));
		} else if (new RegExp("(" + k + ")", (k == "M+" || k == "m+" ? "" : "i")).test(format)) {
			format = format.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k])
				.length)));
		}
	}
	return format;
}


/**
 * @description 
 * @param {Object} time
 * @param {Object} pattern
 */
export function parseTime(time, pattern) {
	if (arguments.length === 0 || !time) {
		return null
	}
	const format = pattern || '{y}-{m}-{d} {h}:{i}:{s}'
	let date
	if (typeof time === 'object') {
		date = time
	} else {
		if ((typeof time === 'string') && (/^[0-9]+$/.test(time))) {
			time = parseInt(time)
		} else if (typeof time === 'string') {
			time = time.replace(new RegExp(/-/gm), '/');
		}
		if ((typeof time === 'number') && (time.toString().length === 10)) {
			time = time * 1000
		}
		date = new Date(time)
	}
	const formatObj = {
		y: date.getFullYear(),
		m: date.getMonth() + 1,
		d: date.getDate(),
		h: date.getHours(),
		i: date.getMinutes(),
		s: date.getSeconds(),
		a: date.getDay()
	}
	const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
		let value = formatObj[key]
		if (key === 'a') {
			return ['', '', '', '', '', '', ''][value]
		}
		if (result.length > 0 && value < 10) {
			value = '0' + value
		}
		return value || 0
	})
	return time_str
}

/**
 * URLmockget
 * @param {Object} url
 */
export function getUrlParam(url) {
	const search = url.split('?')[1]
	if (!search) {
		return {}
	}
	return JSON.parse('{"' + decodeURIComponent(search).replace(/"/g, '\\"').replace(/&/g, '","').replace(/=/g, '":"') +
		'"}')
}