import request from '@/utils/request'

// 
export function getUserInfo(data) {
	return request({
		url: '/user/getUserInfo',
		method: 'post',
		data
	})
}

// 
export function getUserList(params) {
	return request({
		url: '/user/getUserList',
		method: 'get',
		params
	})
}
